//libs
import React from "react";
import { CountryDropdown, RegionDropdown } from "react-country-region-selector";

//consts
import { Images } from "shared/Images";

//styles
import "./style.scss";
import { LABEL, STRINGS } from "shared/Constants";
import DateRangeSelector from "components/DateRangeSelector";
import CustomButton from "components/CustomButton";
import RangeSlider from "components/RangeSlider";

function Filters({
    filterData = {},
    toggle = false,
    handleChangeFilterData=()=>{},
    reset = () => {},
    handleFetchDataForFilter = () => {}
}) {
    return (
        <>
            {toggle ? (
                <div className={`row filters_col ${!toggle ? "d-none" : ""}`}>
                    <div className="row">
                        <div className="col-xl-3 mb-md-0 mb-3 position-relative">
                            <label>{LABEL.COUNTRY}</label>
                            <div className="country_picker ">
                                <CountryDropdown
                                    value={filterData["country"]}
                                    onChange={(val) => {
                                        handleChangeFilterData("country", val)
                                    }}
                                    defaultOptionLabel={LABEL.SELECT}
                                />
                            </div>
                        </div> 
                        <div className="col-xl-3 mb-md-0 mb-3 position-relative">
                            <label>{LABEL.STATE}</label>
                            <div className="country_picker">
                                <RegionDropdown
                                    country={filterData["country"]}
                                    value={filterData["state"]}
                                    onChange={(val) => {
                                        handleChangeFilterData("state", val)
                                    }}
                                    defaultOptionLabel={LABEL.SELECT}
                                    blankOptionLabel={LABEL.SELECT}
                                />
                            </div>
                        </div>
                        <div className="col-xl-2 mb-md-0 mb-3">
                            <DateRangeSelector
                                dateRange={{
                                    startDate: filterData['selectedDate'].from || null,
                                    endDate: filterData['selectedDate'].to || null,
                                    key: 'selection',
                                }}
                                setDateRange={(val) => handleChangeFilterData("selectedDate", {from: val.startDate, to: val.endDate})}
                                titleText={LABEL.START_DATE}
                                icon={Images.calendar}
                            />
                        </div>
                        <div className="col-xl-2 mb-md-0 mb-3">
                            <RangeSlider
                                state={filterData['rateRange']}
                                changeHandler={(val) => handleChangeFilterData("rateRange", val)}
                                titleText={STRINGS.RANGE}
                            />
                        </div>
                        <div className="col mb-md-0 mb-3">
                            <div className="btn_grp d-flex justify-content-end mt-md-4">
                                <CustomButton
                                    Icon={Images.reset}
                                    className="btn btn-sm btn-primary ms-2"
                                    onClick={reset}
                                />
                                <CustomButton
                                    Icon={Images.whiteTick}
                                    className="btn btn-sm btn-primary ms-2"
                                    onClick={handleFetchDataForFilter}
                                />
                            </div>
                           
                        </div>
                    </div>
                </div>
            ) : null}
        </>
    );
}

export default Filters;
