import CustomModal from "components/CustomModal";
import React from "react";
import { useState } from "react";
import ApplyJobform from "./ApplyJobform";
import { NA } from "shared/Constants";
import { dateWithWeekDay } from "shared/Utilities";
import { Pagination } from "components/Pagination";
import './style.scss';

function VacancyCard({
    data = [],
    onCardClick = () => { },
    onApply = () => { },
    jobSubmit = () => { },
}) {
    const [showModal, setShowModal] = useState(false);
    const [selectedJob, setSelectedJob] = useState({});
    const [notesValue, setNotesValue] = useState("");
    const [priceValue, setPriceValue] = useState("");
    const [totalItems, setTotalItems] = useState(0);
    const [itemsPerPage, setItemsPerPage] = useState(15);
    const [activePage, setActivePage] = useState(1)

    const onChangeNote = (value) => {
        setNotesValue(value);
    };
    const onChangePrice = (value) => {
        setPriceValue(value);
    };

    const ApplyJobModal = () => {
        return (
            <CustomModal
                isOpen={showModal}
                medium={true}
                handleToggle={() => setShowModal((prev) => !prev)}
            >
                <ApplyJobform
                    data={selectedJob}
                    priceValue={priceValue}
                    notesValue={notesValue}
                    onChangeNote={onChangeNote}
                    onChangePrice={onChangePrice}
                />
            </CustomModal>
        );
    };

    return (
        <>
            {data.length ? (
                <div className="row job_listing mt-4 mt-md-5">
                    {data?.map((i, index) => {
                        return (
                            <div id={index} className="col-md-4 mb-3">
                                <div className="card">
                                    <div className="card-header text-center" onClick={onCardClick}>
                                        <h2 className="h5 text-capitalize">{i?.job_name ? i?.job_name : NA}</h2>
                                        <span className="text-small">
                                            {i?.created_at ? dateWithWeekDay(i?.created_at) : NA}
                                        </span>
                                    </div>
                                    <div className="card-body p-4">
                                        <h6 className="h6 mb-0 sub_title">Sharief HC - Grove Lane - WA159JX</h6>
                                        <div className="d-flex align-items-center justify-content-between">
                                            <label className="h4 my-3 font-bold">Rate</label>
                                            <span className="h5 my-3">{`${i?.start_rate ? i?.start_rate : 0}-${i?.end_rate ? i?.end_rate : 0
                                                }/hr`}</span>
                                        </div>
                                        <div className="text-center pt-2">
                                            <button
                                                className="btn btn-md btn-outline-secondary"
                                                onClick={() => {
                                                    setSelectedJob(i);
                                                    setShowModal(true);
                                                }}
                                            >
                                                Apply
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    })}

                </div>
            ) : (
                <h6 className="no_result h6 text-center p-5">{"No Vacancy Availiable"}</h6>
            )}
            <ApplyJobModal />
        </>
    );
}

export default VacancyCard;
