const Branches = ({ branchData, handleBranchDataChange }) => {
    const { state } = useLocation();
    const dispatch = useDispatch()
    const history = useHistory()
    const [msg, sendNotifications] = useNotification();
    const [country, setCountry] = useState("");
    const [countryError, setCountryError] = useState("");
    const [description, setDescription] = useState("")
    const [services, setServices] = useState([])
    const [jobTimingData, setJobTimingData] = useState(initialDaysData)

    const selectCountry = (val) => {
        setCountry(val);
    };

    const handleChangeDescription = (e) => {
        handleBranchDataChange("description", e.target.value)
        setDescription(e.target.value);
    }

    const onCheckboxSelect = (e) => {
        setServices((prevState) => {
            let copyPrevState = [...prevState];
            let index = prevState.findIndex((item) => item.id == e.target.id);
            copyPrevState[index].value = e.target.checked;
            console.log(copyPrevState, "prevState");
            return copyPrevState;
        });
    }
    
    const onSubmit = (data, data2) => {
        if ((data?.phone_number?.length && data?.phone_number?.length < 6) || (data?.manager_phone?.length && data?.manager_phone?.length < 6)) {
            return;
        }

        let emptyJobDataArray = checkJobTimingDataValid(jobTimingData);        
        if(emptyJobDataArray.length){
            sendNotifications({
                msg: `Please enter all fields for ${emptyJobDataArray.map(item => ` ${item}`)}`,
                variant: SNACK_VARIANT.ERROR,
            });
            return;
        }

        let notValidJobDataArray = validJobTimingDataForStartEnd(jobTimingData) ;
        if(notValidJobDataArray.length){
            sendNotifications({
                msg: `Please enter valid data for ${notValidJobDataArray.map(item => ` ${item}`)}. Start time must be less than end time`,
                variant: SNACK_VARIANT.ERROR,
            });
            return;
        }

        let payload = { ...data }
        payload.services = payload.services.map(item => ({ id: item.id }))
        payload.job_timings = jobTimingData.filter(item => item.isSelected)
        payload.is_select_all = payload.is_select_all ? 1 : 0
        if (state.data) {
            payload.branch_id = state.data
        }

        dispatch(createOrUpdateBranch(payload, (response) => {
            sendNotifications({
                msg: response.message,
                variant: SNACK_VARIANT.SUCCESS,
            });
            history.push(ADMIN_ROUTES.BRANCHES);
            dispatch(setSelectedTab(STRINGS.BRANCHES))
        }, (error) => {
            sendNotifications({
                msg: error || STRINGS.SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR,
            });
        }))
    };

    const handleSetIsSelectedAll = (value) => {
        handleBranchDataChange("is_select_all", value)
    }

    const handleDisabledHours = (field, day) => {
        if(!jobTimingData?.length){
            return []
        }
        let jobTimingDay = jobTimingData.find(item => item.day ==  day)
        let finalArr = []
        if(field === 'closing_time'){
           let hour = jobTimingDay['opening_time'].split(":")[0];
           for(let i=0; i<= hour; i++){
            finalArr.push(i)
           }
        }
        if(field === 'lunch_start'){
            let hourStart = jobTimingDay['opening_time'].split(":")[0];
            let hourEnd = jobTimingDay['closing_time'].split(":")[0];
            console.log(hourStart, hourEnd, "end")
            for(let i=0; i<= 24; i++){
                if(i<= hourStart || i>= hourEnd){
                    finalArr.push(i)
                }
            }
        }
        if(field === 'lunch_end'){
            let hourStart = jobTimingDay['lunch_start'].split(":")[0];
            let hourEnd = jobTimingDay['closing_time'].split(":")[0];
            console.log(hourStart, hourEnd, "end")
            for(let i=0; i<= 24; i++){
                if(i< hourStart || i>= hourEnd){
                    finalArr.push(i)
                }
            }
        }
        return finalArr;
    }

    const handleDisabledMinutes = (field, day) => {
        if(!jobTimingData?.length){
            return []
        }
        let jobTimingDay = jobTimingData.find(item => item.day ==  day)
        let finalArr = []
        if(field === 'lunch_end'){
            let hourLunchStart = jobTimingDay['lunch_start'].split(":")[0];
            let minuteStart = jobTimingDay['lunch_start'].split(":")[1];
            let minuteEnd = jobTimingDay['closing_time'].split(":")[1];
            let hourLunchEnd = jobTimingDay['lunch_end'].split(":")[0];
            console.log(minuteStart, minuteEnd, "endaaa", hourLunchEnd == hourLunchStart)
            if(hourLunchEnd == hourLunchStart){
                for(let i=0; i<= 60; i++){
                    if(i<= minuteStart){
                        finalArr.push(i)
                    }
                }
            }
        }
        return finalArr;
    }

    useEffect(() => {
        setTimeout(() => {
            dispatch(getServices(
                (response) => {
                    let allData = structureCSInUseData(response.data, "service_name")
                    let selectedData = getSelectedData(allData, branchData.services)
                    setServices(selectedData)
                }
            ))
        }, 10)
    }, [])

    useEffect(() => {
        if (services.length) {
            handleBranchDataChange("services", services.filter(item => item.value));
        }
    }, [services])

    useEffect(() => {
        let selectedData = getSelectedData(services, branchData.services)
        setServices(selectedData)
    }, [services.length, branchData.services.length])

    useEffect(() => {
        if(branchData?.job_timings?.length){
            let newData = [...jobTimingData];
            branchData?.job_timings.forEach(item => {
                newData.splice(item.day, 1, {...item, isSelected: true})
            });
            setJobTimingData(newData)
        }
    }, [branchData.job_timings])

    return (
        <div className="tab_content admin_view field_white">
            <CustomForm
                id={"branch-form"}
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={REGISTRATION_DETAILS_DATA}
                renderSubmitButton={false}
                selectCountry={selectCountry}
                country={branchData["country"]}
                region={branchData["state"]}
                phone={branchData["phone_number"]}
                countryError={countryError}
                defaultValues={{ ...branchData }}
                handleStateDataChange={handleBranchDataChange}
            />
            <h6 className="label_title mb-4">
                <span>{STRINGS.PHARMACY_DESCRIPTION}</span>
            </h6>
            <div className="row mb-4">
                <CustomTextInput
                    titleText={LABEL.DESCRIPITON}
                    type={INPUT_TYPES.TEXT_AREA}
                    onChange={handleChangeDescription}
                    placeholder={PLACEHOLDERS.ENTER_HERE}
                    ShowTextArea={true}
                    className="form-control height-120"
                    value={branchData?.["description"]}
                />
            </div>
            <div className="col-12 mb-3">
                <h6 className="label_title mb-4">
                    <span>{LABEL.SERVICES}</span>
                </h6>
                <div className="row">
                    <CustomCheckBox checkBoxData={services} onCheckboxSelect={onCheckboxSelect} />
                </div>
            </div>
            <div className="col-12 mb-3">
                {console.log(Object.keys(branchData), state.addNewBranch, branchData.is_select_all, "Test bakjtahds")}
                {state.addNewBranch || branchData?.job_timings ? (
                    <JobTiming jobTimingData={jobTimingData} setJobTimingData={setJobTimingData} handleDisabledHours={handleDisabledHours} handleDisabledMinutes={handleDisabledMinutes}/>
                ) : null}
            </div>
            <div className="text-center mt-3">
                <CustomButton
                    className="btn btn-md btn-primary"
                    type={INPUT_TYPES.SUBMIT}
                    form="branch-form"
                    title={state.addNewBranch ? STRINGS.SAVE : STRINGS.UPDATE}
                />
            </div>
        </div>
    );
};

export default Branches;
