import BreadCrumb from 'components/BreadCrumb';
import FunctionalTable from 'components/CustomFunctionalTable';
import DocumentViewerModal from 'components/DocumentViewer';
import JobTimingsView from 'components/JobTimingsView';
import ProfileAttributes from 'components/ProfileAttributes';
import ProfileDescription from 'components/ProfileDescription';
import ProfileDetails from 'components/ProfileDetails';
import useNotification from 'hooks/Notifications';
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom/cjs/react-router-dom.min';
import { getBranch } from 'redux/Actions/PharmacyAdminProfile';
import { ADMIN_ROUTES, BREAD_CRUMB_TEXT, ROUTES, SNACK_VARIANT, STRINGS, USER_TYPES } from 'shared/Constants';
import { Images } from 'shared/Images';
import { getDocsUrl } from 'shared/Utilities';
import { pharmacyDocumentView, strcutureBranchViewData } from 'views/authenticated/constants';

const tableHeaderFunc = ["#", "Document Name", "Uploaded Document", "Status"];

const ViewBranch = () => {
    const history = useHistory();
    const { state } = useLocation();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const profile = useSelector(state => state.common.profileData)
    const [branchData, setBranchData] = useState({})
    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [modalOpacity, setModalOpacity] = useState(false);

    const handleDocumentClick = (a, b) => {
        if (a.document) {
            setDocumentUrl(getDocsUrl(a.document));
            setModalOpacity(true);
            setViewDoc(true);
        }
    };

    const handleBreadCrumbClick = () => {
        history.push(ADMIN_ROUTES.BRANCHES)
    }

    useEffect(() => {
        dispatch(getBranch({ id: state.data }, (response) => {
            const {data} = response;
            console.log(data, "data  dsfhhfdj")
            setBranchData(data)
        }, (error) => {
            sendNotifications({
                msg: error || STRINGS.SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR,
            });
        }));
    }, [])

  return (
    <main className="main">
            {/* <Header /> */}

            <div className="inner_content view_profile_sec">
                <div className="container">
                    <BreadCrumb
                        onClick={handleBreadCrumbClick}
                        previousLink={BREAD_CRUMB_TEXT.PHARMACY_BRANCHES}
                        currentLink={`View ${BREAD_CRUMB_TEXT.BRANCH}`}
                    />
                    <div className="d-flex justify-content-between mt-3">
                        <div className="title_h1 mb-4">
                            <h1 className="h6">View Profile</h1>
                        </div>
                        <button
                            className="btn btn-xs btn-primary"
                            onClick={() => {
                                history.push({pathname: ROUTES.MANAGE_BRANCH, state: {data: state.data}})
                            }}
                        >
                            <img src={Images.EditIcon} alt="" width={20} />
                        </button>
                    </div>
                    <ProfileDetails data={strcutureBranchViewData(profile, branchData)} imageURL={getDocsUrl(profile?.owner_info?.photo)} verifiedName={profile.verified_document} profileCompleteness={profile.profile_completeness} rating={3} />
                    <ProfileDescription
                        title={"Descriptions"}
                        description={branchData?.description || ""}
                    />
                    <ProfileAttributes
                        title={"Services:"}
                        attributes={branchData.services || []}
                        field={'service_name'}
                    />
                    <JobTimingsView
                        data={branchData.job_timings}
                    />
                    <FunctionalTable
                        theadData={tableHeaderFunc}
                        tbodyData={pharmacyDocumentView(profile?.pharmacy_info?.documents)}
                        // handleActionClick={handleActionClick}
                        handleDocumentClick={handleDocumentClick}
                        type={USER_TYPES.PHARMASY_ADMIN}
                    />
                </div>
            </div>
            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    documentUrl={documentUrl}
                    setViewDoc={setViewDoc}
                    modalOpacity={modalOpacity}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
        </main>
  )
}

export default ViewBranch