// libs
import React from "react";
import PhoneInput from "react-phone-input-2";

// wigets
import CustomTextInput from "components/CustomTextInput";
import JobTiming from "components/JobTimings";
import ReactSelect from "components/ReactSelect";
import SearchBar from "components/SearchBar";

const StaffDetails = ({
    fetchStaffDetailsByEmail = () => { },
    staffDetails = {},
    handleStaffInputsChange = () => { },
    profile = {},
    branchesData = {},
    handleBranchInputChange = () => { },
    setJobTimings = () => { },
    handleSetIsSelectedAll = () => { },
    jobTimingsData = [],
    disabledInputs = {},
    isView = false,
    isEdit = false,
    handleDisabledHours = () => {},
    handleDisabledMinutes = () => {}
}) => {
    const enabledDays = jobTimingsData?.map((day) => day?.day);
    return <div className="pt-2 admin_view">
        <div className="field_white mb-3">
            <h6 className="label_title mb-4">
                <span>User Details</span>
            </h6>
            <div className="row">
                <div className="col-md-4 mb-4">
                    <label>Staff Email</label>
                    {isEdit ?
                        <CustomTextInput
                            type="text"
                            name="email"
                            placeholder={"Enter Here..."}
                            value={staffDetails?.locumDetails?.email}
                            disabled={disabledInputs?.email}
                        /> :
                        <SearchBar
                            searchAPI={fetchStaffDetailsByEmail}
                            keyword={staffDetails?.locumDetails?.email}
                            handleChangeKeyword={handleStaffInputsChange}
                            name={"email"}
                            placeholder="Enter Here..."
                            isSearchBarIcon={false}
                            debounceTime={500}
                            disabled={disabledInputs?.email}
                        />}
                </div>
                <div className="col-md-4 mb-4">
                    <label>Staff Username</label>
                    <CustomTextInput
                        type="text"
                        name="username"
                        placeholder={"Enter Here..."}
                        onChange={handleStaffInputsChange}
                        value={staffDetails?.locumDetails?.username}
                        disabled={disabledInputs?.username}
                    />
                </div>
                <div className="col-md-4 mb-4">
                    <label>Phone Number</label>
                    <div className="country_dropdown">
                        <PhoneInput
                            inputProps={{
                                name: "phone",
                            }}
                            value={staffDetails?.locumDetails?.phone || null}
                            name="phone"
                            onChange={handleStaffInputsChange}
                            disabled={disabledInputs?.phone}
                        />
                    </div>
                </div>
            </div>
        </div>

        <div className="field_white mb-3">
            <h6 className="label_title mb-4">
                <span>Pharmacy Details</span>
            </h6>
            <div className="row">
                <div className="col-md-4 mb-4">
                    <label>Pharmacy</label>
                    <CustomTextInput
                        type="text"
                        placeholder={"Pharmacy Name"}
                        value={profile?.pharmacy_info?.pharmacy_name || ""}
                        disabled={true}
                    />
                </div>
                <div className="col-md-4 mb-4">
                    <label>Branch</label>
                    {isView ?
                        <CustomTextInput
                            type="text"
                            placeholder={"Branch"}
                            value={staffDetails?.branch || null}
                            disabled={true}
                        />
                        : <ReactSelect       
                            hideSelectedOptions={false}                     
                            selectedOption={staffDetails?.branch || null}
                            handleChange={handleBranchInputChange}
                            options={branchesData}
                            title={"Select Branch"}
                            disabled={disabledInputs?.branch}
                            isMulti={true}
                            closeMenuOnSelect={false}
                            isSearchable={true}
                            // menuIsOpen={true}
                            showCheckbox = {true}
                            showNumberSelected = {true}
                        />}
                </div>
            </div>
        </div>

        {/* <div className="field_white mb-3">
            {jobTimingsData ? <JobTiming
                data={jobTimingsData || []}
                setJobTimingData={setJobTimings}
                handleSetIsSelectedAll={handleSetIsSelectedAll}
                isSelectAll={staffDetails?.isSelectAll}
                disabled={disabledInputs?.jobTimings}
                enabledDays={enabledDays || []}
                handleDisabledHours={handleDisabledHours}
                handleDisabledMinutes={handleDisabledMinutes}
            /> : null}
        </div> */}
    </div>
};

export default StaffDetails;