import CustomButton from "components/CustomButton";
import CustomCheckBox from "components/CustomCheckBox";
import CustomTextInput from "components/CustomTextInput";
import ReactSelect from "components/ReactSelect";
import React, { useState } from "react";
import { INPUT_TYPES, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import { dateWithMonthString } from "shared/Utilities";

function ApplyJobform({
    notesValue = "",
    priceValue = "",
    onChangeNote = () => {},
    onChangePrice = () => {},
    data = {},
}) {
    const [addNote, setAddNote] = useState(false);
    const [appliedJobs, setAppliedJobs] = useState({});

    const options = [
        {
            value: "monday",
            label: "Monday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "tuesday",
            label: "Tuesday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "wednesday",
            label: "Wednesday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "thursday",
            label: "Thursday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "friday",
            label: "Friday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "saturday",
            label: "Saturday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
        {
            value: "sunday",
            label: "Sunday Time: 09:00 AM - 6:00 PM         Lunch: 1:00 AM - 2:00 PM",
        },
    ];
    const handleBranchInputChange = (value) => {
        setAppliedJobs((prev) => {
            return {
                ...prev,
                days: value,
                isSelectAll: value?.details?.job_timings?.length == 7 ? true : false,
            };
        });
    };

    return (
        <div>
            <div className="confirmation_modal text-center p-4">
                <h2 className="h5 mb-2">{"Apply for Job"}</h2>
                <div className="align-items-baseline">
                    <p className="mb-4">{`Original Rate ${
                        data?.start_rate ? data?.start_rate : 0
                    }-${data?.end_rate ? data?.end_rate : 0}/hr`}</p>
                    <p>
                        Please Note: This is not a guarantee that your request will be accepted.
                        Your request either be accepted or rejected.
                    </p>

                    <p>{`Shift Timing (${dateWithMonthString(
                        data?.start_date,
                    )} to ${dateWithMonthString(data?.end_date)}).`}</p>
                    <ReactSelect
                        hideSelectedOptions={false}
                        selectedOption={appliedJobs.days || null}
                        handleChange={handleBranchInputChange}
                        options={options}
                        title={"Select Days"}
                        isMulti={true}
                        closeMenuOnSelect={false}
                        isSearchable={true}
                        // menuIsOpen={true}
                        showCheckbox={true}
                        showNumberSelected={true}
                    />

                    <p>Proposed Rate: Please enter the rate you would like to suggest.</p>
                    <CustomTextInput
                        onChange={onChangePrice}
                        value={priceValue || ""}
                        titleText={"Rate"}
                        placeholder={STRINGS.ENTER_HERE}
                    />
                    <div className="checkbox mb-3">
                        <input
                            checked={addNote}
                            type={INPUT_TYPES.CHECKBOX}
                            onChange={() => setAddNote((prev) => !prev)}
                        />
                        <label>{"Add a Note"}</label>
                    </div>

                    {addNote ? (
                        <CustomTextInput
                            titleText={"Note"}
                            ShowTextArea={true}
                            value={notesValue || ""}
                            onChange={onChangeNote}
                            placeholder={STRINGS.ENTER_HERE}
                        />
                    ) : null}
                    <p>
                        Please Note: Requests for extra expense detailed in notes will be ignored.
                    </p>
                </div>
                <button className="btn btn-md btn-secondary">Send Request</button>
            </div>
        </div>
    );
}

export default ApplyJobform;
